import { createContext, useContext, useEffect, useState } from "react";
import axios from "axios";

type TypeCoinInfo = {
  name: string;
  symbol: string;
  url_logo: string;
  price?: number;
  price24hPcnt?: number;
  turnover24h?: number;
  lowPrice24h?: number;
};

type TypeCoinName = { name: string; symbol: string; url_logo: string };

interface RoleContextType {
  role: string;
  coinPrices: TypeCoinInfo[];
  setRole: (role: string) => void;
}

const RoleContext = createContext<RoleContextType | undefined>(undefined);
export const RoleProvider = ({ children }: { children: React.ReactNode }) => {
  const [role, setRole] = useState<string>("guest"); // –ü–æ —É–º–æ–ª—á–∞–Ω–∏—é "guest"
  const [coinPrices, setCoinPrices] = useState<TypeCoinInfo[]>([]);

  useEffect(() => {
    const savedRole = localStorage.getItem("role");
    if (savedRole) {
      setRole(savedRole); // –ï—Å–ª–∏ –≤ LocalStorage –µ—Å—Ç—å —Ä–æ–ª—å ‚Äî –±–µ—Ä–µ–º –µ—ë
    } else {
      setRole("guest"); // –ï—Å–ª–∏ –Ω–µ—Ç ‚Äî —Å—Ç–∞–≤–∏–º "guest"
      localStorage.setItem("role", "guest"); // –ò —Å—Ä–∞–∑—É —Å–æ—Ö—Ä–∞–Ω—è–µ–º –≤ LocalStorage
    }

    // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –∑–∞–≥—Ä—É–∑–∫–∏ –¥–∞–Ω–Ω—ã—Ö –∏–∑ –ë–î –∏ –Ω–∞—á–∞–ª—å–Ω—ã—Ö —Ü–µ–Ω
    const fetchCoins = async () => {
      try {
        // –ü–æ–ª—É—á–∞–µ–º —Å–ø–∏—Å–æ–∫ –º–æ–Ω–µ—Ç –∏–∑ –ë–î
        const { data: coins } = await axios.get<TypeCoinName[]>(
          "http://127.0.0.1:8000/market"
        );
        console.log("üìå –ü–æ–ª—É—á–µ–Ω—ã –º–æ–Ω–µ—Ç—ã:", coins);

        // –ü–æ–ª—É—á–∞–µ–º –º–µ—Ç–∞-–∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –∏ –Ω–∞—á–∞–ª—å–Ω—ã–µ —Ü–µ–Ω—ã
        const { data: metaInfo } = await axios.get(
          "http://127.0.0.1:8000/market/metainfo"
        );
        const { data: initialPrices } = await axios.get(
          "http://127.0.0.1:8000/market/prices"
        );

        console.log("üîç Meta Info:", metaInfo);
        console.log("üí∞ Initial Prices:", initialPrices);

        // –§–æ—Ä–º–∏—Ä—É–µ–º –º–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤
        const updatedPrices: TypeCoinInfo[] = coins.map((coin, index) => ({
          ...metaInfo[index], // –ú–µ—Ç–∞–¥–∞–Ω–Ω—ã–µ
          price: 0,
          price24hPcnt: 0,
          turnover24h: 0,
          lowPrice24h: 0,
        }));

        console.log(updatedPrices);

        // –û–±–Ω–æ–≤–ª—è–µ–º —Å—Ç–µ–π—Ç –∏ —Ä–µ—Ñ–µ—Ä–µ–Ω—Å
        setCoinPrices(updatedPrices);
      } catch (error) {
        console.error("‚ùå –û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏ –º–æ–Ω–µ—Ç:", error);
      }
    };

    // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –ø–µ—Ä–∏–æ–¥–∏—á–µ—Å–∫–æ–≥–æ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è —Ü–µ–Ω –∏–∑ Redis
    const updatePriceFromRedis = async () => {
      try {
        const { data: responce } = await axios.get(
          "http://127.0.0.1:8000/market/prices"
        );
        console.log(responce, "ahui");

        setCoinPrices((prev) =>
          prev.map((coin) => ({
            ...coin,
            price24hPcnt: responce[coin.symbol + "USDT"]?.price24hPcnt,
            price: responce[coin.symbol + "USDT"]?.price || 50,
            turnover24h: responce[coin.symbol + "USDT"]?.turnover24h,
            lowPrice24h: responce[coin.symbol + "USDT"]?.lowPrice24h,
          }))
        );
      } catch (error) {
        console.error("‚ùå –û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏ —Ü–µ–Ω –∏–∑ Redis:", error);
      }
    };
    fetchCoins();
    const interval = setInterval(updatePriceFromRedis, 3000); // –û–±–Ω–æ–≤–ª—è–µ–º —Ü–µ–Ω—ã –∫–∞–∂–¥—ã–µ 3 —Å–µ–∫—É–Ω–¥—ã

    return () => clearInterval(interval); // –û—á–∏—Å—Ç–∫–∞ –∏–Ω—Ç–µ—Ä–≤–∞–ª–∞ –ø—Ä–∏ —Ä–∞–∑–º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏
  }, []);

  const updateRole = (newRole: string) => {
    setRole(newRole);
    localStorage.setItem("role", newRole);
  };

  return (
    <RoleContext.Provider value={{ role, setRole: updateRole, coinPrices }}>
      {children}
    </RoleContext.Provider>
  );
};

export const useRole = () => {
  const context = useContext(RoleContext);
  if (!context) {
    throw new Error("useRole must be used within a RoleProvider");
  }
  return context;
};
